			}else{
				this.$ajax("/cart/unselect","POST",{skuId:s.skuId}).then(res=>{
					if(res){
						_this.trolley[i].selectFlag = 0
						return false
					}else{
						uni.showToast({
							"title":"无法取消，请再尝试",
