					if(res.msg == "success"){
						uni.showToast({
							"title":"修改成功",
							"icon":"none"
						})
						_this.trolley[i].count = data
					}else{
						uni.showToast({
							"title":"添加失败",
							"icon":"none"
						})
						_this.$refs.counter.plus(s.count);
					}
				})
			}else{
				this.$ajax("/cart/add","POST",{skuId:s.skuId,count:1}).then((res)=>{
					if(res.msg == "success"){
