		getTrolley(){
			let _this = this;
			this.$ajax("/cart/getCart","POST",{}).then(res=>{
				if(res != "查询出错"){
					uni.showToast({
						"title":"加载成功",
						"icon":"none"
					})
					
					_this.selectAllFlag = res.selectAllFlag;
					_this.trolley = [];
					res.cartItems.map(res=>{
						_this.trolley.push(res);
					})
					
						
					
					
					
					console.log(_this.trolley.length);
				}else{
					uni.showToast({
						"title":"加载失败",
						"icon":"none"
					})
					
				}
